/**
 * 
 *  fetch api 
 */

class RequestManager {
  /**
   * get 
   */
  static fetchGet(data) {
    let u = setUrlSearchParams(data);
    return fetchW('/video/v5video/item/search.htm?' + u, {
      credentials: 'include'
    });
  }

  /**
   * post 
   */
  static fetchPost(data) {
    return fetchW('/video/v5video/item/add.htm', {
      method: 'POST',
      body: setUrlSearchParams(data),
      credentials: 'include'
    });
  }

  /**
   *  post
   */
  static postNewTheme(data) {
    return fetchW('/topic/rule/add.htm', {
      method: 'POST',
      body: setUrlSearchParams({data: JSON.stringify(data)}),
      credentials: 'include'
    });
  }

  /**
   *  post
   */
  static postEditTheme(data) {
    return fetchW('/topic/rule/edit.htm', {
      method: 'POST',
      body: setUrlSearchParams({data: JSON.stringify(data)}),
      credentials: 'include'
    });
  }

  /**
   *  post
   */
  static postNewTopic(data) {
    return fetchW('/topic/rule/topic/add.htm', {
      method: 'POST',
      body: setUrlSearchParams({data: JSON.stringify(data)}),
      credentials: 'include'
    });
  }

  /**
   *  get
   */
  static getProgramList(data) {
    let u = setUrlSearchParams(data);
    return fetchW('/topic/rule/getPrograms.htm?' + u, {
      credentials: 'include'
    });
  }

  /**
   *  post
   */
  static postExcludePrograms(data) {
    let u = setUrlSearchParams(data);
    return fetchW('/topic/rule/program/exclude.htm?' + u, {
      method: 'POST',
      credentials: 'include'
    });
  }

}

/**
 *  fetch 
 */
function fetchW(req, opt) {
  return fetch(req, opt)
  .then(checkStatus)
  .catch(err => {
    console.error('fetch failed', err)
  })
  .then(parseJSON)
  .then(data => {
    console.log('fetch succeeded with JSON response, ', data);
    return data;
  });
}

/**
 * change data to URLSearchParams
 */
function setUrlSearchParams(data) {
  let urlSearchParams = new URLSearchParams();
  for (let name in data) {
    urlSearchParams.append(name, data[name]);
  }
  return urlSearchParams;
}

/**
 * change data to formData
 */
function setFormData(data) {
  let formData = new FormData();
  for (let name in data) {
    formData.append(name, data[name]);
  }
  return formData;
}

/**
 * Promise ,  fetch 
 */
function setPromiseTimeout(promise, ms) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      reject('request timeout');
    }, ms);
    promise.then(resolve, reject);
  });
}

/**
 * fetch  reject http error status  404  500
 *  response 
 */
function checkStatus(response) {
  if (response.status >= 200 && response.status < 300) {
    return response;
  } else {
    var error = new Error(response.statusText);
    error.response = response;
    throw error;
  }
}

/**
 *  response  json
 */
function parseJSON(response) {
  return response.json()
}

export default RequestManager;




